import { useEffect, useState, useContext, useRef } from "react";
import { useParams } from "react-router-dom";
import Header from "../components/Header";
import Footer from "../components/Footer";
import Loader from "../components/Loader";
import { LanguageContext } from "../contexts/LanguageContext";
import PageTitleCard from "../components/PageTitleCard";

function AartiDetail() {
  const { id } = useParams();
  const [detail, setDetail] = useState(null);
  const [loading, setLoading] = useState(true);
  const [isPlaying, setIsPlaying] = useState(false);
  const audioRef = useRef(null);
  const [currentAudio, setCurrentAudio] = useState(null);
  const [showAudioPlayer, setShowAudioPlayer] = useState(false);
  const [currentTime, setCurrentTime] = useState(0);
  const [duration, setDuration] = useState(0);

  const { language, setLanguage, fontSize, setFontSize } = useContext(LanguageContext);

  useEffect(() => {
    fetch(`https://api.bhaktibhav.app/frontend/arti/${id}`)
      .then((res) => res.json())
      .then((data) => {
        if (data?.status === "success" && data.data) {
          setDetail(data.data);
          console.log("Fetched aarti detail:", data.data);
        } else {
          setDetail(null);
          alert("No data found!");
        }
        setLoading(false);
      })
      .catch((err) => {
        console.error(err);
        setLoading(false);
      });
  }, [id]);

  // Cleanup effect to stop audio when component unmounts or page changes
  useEffect(() => {
    const cleanup = () => {
      console.log("Aarti cleanup running...");
      if (audioRef.current) {
        audioRef.current.pause();
        audioRef.current.currentTime = 0;
        // Clear all event listeners
        audioRef.current.onloadedmetadata = null;
        audioRef.current.ontimeupdate = null;
        audioRef.current.onended = null;
        audioRef.current.onpause = null;
        audioRef.current.onplay = null;
        audioRef.current.onerror = null;
        audioRef.current = null;
      }
      // Reset all audio-related states
      setCurrentAudio(null);
      setShowAudioPlayer(false);
      setIsPlaying(false);
      setCurrentTime(0);
      setDuration(0);
    };

    // Add beforeunload event listener
    const handleBeforeUnload = () => {
      cleanup();
    };

    window.addEventListener('beforeunload', handleBeforeUnload);

    // Return cleanup function
    return () => {
      window.removeEventListener('beforeunload', handleBeforeUnload);
      cleanup();
    };
  }, []);


  const handlePlay = () => {
    const url = detail.audioUrl?.startsWith("http")
      ? detail.audioUrl
      : `https://api.bhaktibhav.app${detail.audioUrl}`;
    
    if (!url) return;
    
    // If audio player is already showing for this audio
    if (currentAudio === url && showAudioPlayer) {
      // Hide the audio player and stop audio
      if (audioRef.current) {
        audioRef.current.pause();
        // Clear event listeners before setting to null
        audioRef.current.onloadedmetadata = null;
        audioRef.current.ontimeupdate = null;
        audioRef.current.onended = null;
        audioRef.current.onpause = null;
        audioRef.current.onplay = null;
        audioRef.current.onerror = null;
        audioRef.current = null;
      }
      setShowAudioPlayer(false);
      setCurrentAudio(null);
      setIsPlaying(false);
      setCurrentTime(0);
      setDuration(0);
    } else {
      // Show audio player and start playing
      if (audioRef.current) {
        audioRef.current.pause();
        // Clear existing event listeners
        audioRef.current.onloadedmetadata = null;
        audioRef.current.ontimeupdate = null;
        audioRef.current.onended = null;
        audioRef.current.onpause = null;
        audioRef.current.onplay = null;
        audioRef.current.onerror = null;
      }
      
      audioRef.current = new Audio(url);
      setCurrentAudio(url);
      setShowAudioPlayer(true);
      setIsPlaying(true);
      audioRef.current.play();

      // Audio event listeners
      audioRef.current.onloadedmetadata = () => {
        if (audioRef.current) {
          setDuration(audioRef.current.duration);
        }
      };

      audioRef.current.ontimeupdate = () => {
        if (audioRef.current) {
          setCurrentTime(audioRef.current.currentTime);
        }
      };

      audioRef.current.onended = () => {
        // Auto-hide when audio completes
        setCurrentAudio(null);
        setShowAudioPlayer(false);
        setIsPlaying(false);
        setCurrentTime(0);
        setDuration(0);
      };

      audioRef.current.onpause = () => {
        setIsPlaying(false);
      };

      audioRef.current.onplay = () => {
        setIsPlaying(true);
      };

      audioRef.current.onerror = () => {
        setIsPlaying(false);
        setShowAudioPlayer(false);
        setCurrentAudio(null);
        setCurrentTime(0);
        setDuration(0);
      };
    }
  };

  const handlePlayerPlayPause = () => {
    if (!audioRef.current) return;
    
    if (isPlaying) {
      audioRef.current.pause();
      setIsPlaying(false);
    } else {
      audioRef.current.play();
      setIsPlaying(true);
    }
  };

  const handleCloseAudioPlayer = () => {
    if (audioRef.current) {
      audioRef.current.pause();
      // Clear event listeners before setting to null
      audioRef.current.onloadedmetadata = null;
      audioRef.current.ontimeupdate = null;
      audioRef.current.onended = null;
      audioRef.current.onpause = null;
      audioRef.current.onplay = null;
      audioRef.current.onerror = null;
      audioRef.current = null;
    }
    setCurrentAudio(null);
    setShowAudioPlayer(false);
    setIsPlaying(false);
    setCurrentTime(0);
    setDuration(0);
  };

  const handleSeek = (e) => {
    if (audioRef.current && duration > 0) {
      const seekTime = (e.target.value / 100) * duration;
      audioRef.current.currentTime = seekTime;
      setCurrentTime(seekTime);
    }
  };

  const formatTime = (time) => {
    if (isNaN(time)) return "0:00";
    const minutes = Math.floor(time / 60);
    const seconds = Math.floor(time % 60);
    return `${minutes}:${seconds.toString().padStart(2, '0')}`;
  };

  if (loading) return <Loader message=" Loading   " size={200} />;
  if (!detail)
    return <p className="text-center py-10 theme_text"> No data found!</p>;

  const jsonFile = {
    "share": {
      "hi": "'ks;j djsa",
      "en": "Share"
    },
    "listen": {
      "hi": "vkjrh lqusa",
      "en": "Listen"
    },
    "pause": {
      "hi": "can djsa" ,
      "en": "Pause"
    }
  }
  const shareText = ` ${detail.name?.en || "Aarti"} \n\n${detail.text?.hi || ""}\n\nListen here: ${window.location.href}`;
  const handleNativeShare = async () => {
    if (navigator.share) {
      try {
        await navigator.share({
          title: `${detail.name?.en || "Aarti"}`,
          text: shareText,
          url: window.location.href,
        });
      } catch (err) {
        console.error("Share failed:", err);
      }
    } else {
      alert("Sharing is not supported on this browser.");
    }
  };

  return (
    <>
      <Header pageName={{ hi: "vkjrh", en: "Aarti" }} hindiFontSize="true" />
      <PageTitleCard
        titleHi={"vkjrh"}
        titleEn={"Aarti"} 
        customEngFontSize={"14px"}
        customFontSize={"23px"}
        
      />
      <div className="container mx-auto px-4"> 
        {detail.imageUrl && (
          <img
            src={
              detail.imageUrl.startsWith("http")
                ? detail.imageUrl
                : `https://api.bhaktibhav.app${detail.imageUrl}`
            }
            alt={detail.name?.en || "Aarti Image"}
            className="max-w-[300px] max-h-[300px] mx-auto mt-4 rounded-lg shadow-lg"
          />
        )}

        <div className="flex justify-center gap-4 my-6">
          <div className="mt-4">

            <button className={`bg-[#9A283D] text-white px-6 py-2 rounded-full shadow flex items-center ${language === "hi" ? "font-hindi" : "font-eng"}`}
            onClick={handleNativeShare}
            >
              <img src="../img/share_icon.png" alt="" className="w-[15px] h-[15px] mr-2" /> {language === "hi" ? jsonFile.share.hi : jsonFile.share.en}
            </button>
          </div>

          <div className="mt-4">
            {detail.audioUrl && (
              <button
                onClick={handlePlay}
                className={`px-6 py-2 rounded-full shadow flex items-center ${language === "hi" ? "font-hindi" : "font-eng"} ${
                  showAudioPlayer ? "bg-red-600 text-white" : "bg-[#9A283D] text-white"
                }`}
              >
                {showAudioPlayer ? (
                  <>
                    <span className="audio_pause_icon mr-2"></span> {language === "hi" ? jsonFile.pause.hi : jsonFile.pause.en}
                  </>
                ) : (
                  <>
                    <span className="audio_icon mr-2"></span> {language === "hi" ? jsonFile.listen.hi : jsonFile.listen.en}
                  </>
                )}
              </button>
            )}

            <audio ref={audioRef} className="hidden">
              <source src={
                detail.audioUrl?.startsWith("http")
                  ? detail.audioUrl
                  : `https://api.bhaktibhav.app${detail.audioUrl}`
              } type="audio/mpeg" />
              Your browser does not support the audio element.
            </audio>
          </div>
        </div>

        {/* Audio Player Preview */}
        {showAudioPlayer && (
          <div className="flex justify-center mt-6">
            <div className="bg-white border-2 border-[#9A283D] rounded-xl p-4 shadow-lg" style={{ width: '100%', maxWidth: '400px' }}>
              <div className="flex items-center justify-between mb-3">
                <h3 className="font-hindi text-[#9A283D] font-semibold text-sm">vkjrh </h3>
                <button 
                  onClick={handleCloseAudioPlayer}
                  className="text-[#9A283D] hover:text-red-600 transition-colors duration-200"
                >
                  <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                  </svg>
                </button>
              </div>
              
              <div className="flex items-center gap-3">
                <button 
                  onClick={handlePlayerPlayPause}
                  className="bg-[#9A283D] text-white rounded-full p-3 hover:bg-[#7A1F2D] transition-colors duration-200 flex-shrink-0"
                >
                  {isPlaying ? (
                    <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 24 24">
                      <path d="M6 4h4v16H6V4zm8 0h4v16h-4V4z"/>
                    </svg>
                  ) : (
                    <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 24 24">
                      <path d="M8 5v14l11-7z"/>
                    </svg>
                  )}
                </button>
                
                <div className="flex-1">
                  <input
                    type="range"
                    min="0"
                    max="100"
                    value={duration ? (currentTime / duration) * 100 : 0}
                    onChange={handleSeek}
                    className="w-full h-2 bg-gray-200 rounded-lg appearance-none cursor-pointer"
                    style={{
                      background: `linear-gradient(to right, #9A283D 0%, #9A283D ${duration ? (currentTime / duration) * 100 : 0}%, #e5e7eb ${duration ? (currentTime / duration) * 100 : 0}%, #e5e7eb 100%)`
                    }}
                  />
                  <div className="flex justify-between text-xs text-gray-500 mt-2 font-eng">
                    <span>{formatTime(currentTime)}</span>
                    <span>{formatTime(duration)}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        )}

        <div className={`theme_text leading-loose mt-4 text-center ${fontSize}  ${language === "hi" ? "font-hindi" : "font-eng"}`}>
          {language === "hi"
            ? 
            <div dangerouslySetInnerHTML={{ __html: detail.description?.hi.replace(/\.\.\./g, "---").replace(/,/g, "]") }} /> 
            :  
            <div dangerouslySetInnerHTML={{ __html: detail.description?.en }} />
          }
        </div>

        {/* Text */}
        {detail.text && (
          <div className="mt-8 bg-yellow-50 p-4 rounded-lg shadow max-w-2xl mx-auto">
            <pre
              className={`whitespace-pre-wrap leading-relaxed ${language === "hi" ? `font-hindi ${fontSize}` : `italic font-eng ${fontSize}`}`}
            >
              {language === "hi" ? detail.text.hi : detail.text.en}
            </pre>
          </div>
        )}


      </div>

      
    </>
  );
}

export default AartiDetail;
